import NavBar from "./components/navigation";
import Footer from "./components/footer";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./components/pages/Home";
import Blog from "./components/pages/Blog";
import Services from "./components/pages/Services";
import Contact from "./components/pages/Contact-us";
import Conditions from "./components/pages/Terms-Conditions";
import Privacy from "./components/pages/Privacy-Policy";
import About from "./components/pages/About";

function App() {
  return (
    <>
      <Router>
        <NavBar />
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/blog" component={Blog} />
          <Route path="/services" component={Services} />
          <Route path="/privacy-policy" component={Privacy} />
          <Route path="/terms-conditions" component={Conditions} />
          <Route path="/contact-us" component={Contact} />
          <Route path="/about" component={About} />
        </Switch>
      </Router>
      <Footer />
    </>
  );
}

export default App;
